:root {
  --Dark-Grayish: hsl(217, 19%, 35%);
  --Desaturated-Blue: hsl(214, 17%, 51%);
  --Grayish: hsl(212, 23%, 69%);
  --Blue: hsl(210, 46%, 95%);
}

:root {
  --font-inter: font-family: "Outfit", sans-serif;
}

html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: var(--Blue);
}

